app-candidates
{
	#content {
		display: flex;
		flex-direction: column;
		background: #EEE;
	}

	#content > main {
		padding: 2rem;
	}

	#content > header {
		padding : 1rem;
	}

	#content header {
		background : #FFF;

		display : flex;
		align-items: center;
	}

	.card-custom
	{
		color : #FFF;

		h4
		{
			font-size : 2rem;
		}

		h5
		{
			font-size : .8rem;
			text-transform: uppercase;
			font-weight: bold;
			text-align: right;
		}
	}
}